@import url(https://fonts.googleapis.com/css2?family=VT323&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* General */

* {
  box-sizing: border-box;
}

body {
  margin: auto;
  background-color: #222;
}

.App {
  padding: 2rem;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateY(-50%) translateX(-50%);
  background-color: #1c1c1c;
}

.App * {
  font-family: 'VT323', monospace;
  font-size: 2rem;
}

/* Display */

.calc-display {
  max-width: 20rem;
  color:#aff;
  text-align: right;
  word-wrap: break-word;
  background-color: #111;
}

.input {
  width: 100%;
  font-size: 1.5rem;
  min-height: 1rem;
}

.output {
  font-size: 2.5rem;
  margin-bottom: 2rem;
  color: #fff;
}

/* Numpad */

#NumPad {
  width: 20rem;
}

.padButton button {
  width: 5rem;
  height: 5rem;
  float: left;
}

#clear-div button, #zero-div button {
  width: 10rem;
}

#equals-div button {
  height: 10rem;
}

#zero-div button {
  margin-top: -5rem;
}

#decimal-div button {
  margin-top: -5rem;
  margin-left: 10rem;
}

#clear {
  background-color: #944;
}

#equals {
  background-color: #449;
}

#divide, #multiply, #subtract, #add {
  background-color: #bbb;
}

/* Author */

#author {
  color: #eee;
  text-align: center;
  margin-top: 28.5rem;
}

#author, #author a {
  font-size: 1.1rem;
  font-family: Helvetica, Arial, sans-serif;
  font-weight: bold;
}
